  .globalHeader__hamburger {
    position: absolute;
    top: 50%;
    right: 15px;
    width: 20px;
    height: 16px;
    transform: translateY(-50%); }
    .globalHeader__hamburger::before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 3px;
      border-radius: 3px;
      background-color: #ccc;
      transition: all .3s; }
    .globalHeader__hamburger::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 3px;
      border-radius: 3px;
      background-color: #ccc;
      transition: all .3s; }
    .globalHeader__hamburger span {
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      height: 3px;
      border-radius: 3px;
      background-color: #ccc;
      transform: translateY(-50%); }
    .globalHeader__hamburger.-active {
      transform: translateY(-50%) rotate(45deg); }
      .globalHeader__hamburger.-active::before {
        top: 50%;
        bottom: auto;
        transform: translateY(-50%) rotate(90deg); }
      .globalHeader__hamburger.-active::after {
        top: 50%;
        transform: translateY(-50%); }
      .globalHeader__hamburger.-active span {
        opacity: 0; }
  .globalHeader__title {
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    padding: 5px;
    text-align: center; }
